import React, { useState } from 'react';
import { Outlet, Link, useLocation } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Container, Box, IconButton, Drawer, List, ListItem, ListItemText, useMediaQuery, useTheme } from '@mui/material';
import { Menu as MenuIcon } from '@mui/icons-material';
import { motion } from 'framer-motion';

function Layout() {
    const [mobileOpen, setMobileOpen] = useState(false);
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('md'));
    const location = useLocation();

    const menuItems = [
        { text: 'Accueil', path: '/' },
        { text: 'Expriences', path: '/experiences' },
        { text: 'Logiciels', path: '/software' },
        { text: 'IDEs', path: '/ides' },
        { text: 'Langages', path: '/languages' },
        { text: 'Projets', path: '/projects' },
        { text: 'Coordonnes', path: '/contact' },
    ];

    const handleDrawerToggle = () => {
        setMobileOpen(!mobileOpen);
    };

    return (
        <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
            <AppBar position="sticky" sx={{ backgroundColor: '#2c3e50' }}>
                <Toolbar>
                    {isMobile && (
                        <IconButton
                            color="inherit"
                            edge="start"
                            onClick={handleDrawerToggle}
                            sx={{ mr: 2 }}
                        >
                            <MenuIcon />
                        </IconButton>
                    )}
                    
                    <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                        <motion.div
                            whileHover={{ scale: 1.1 }}
                            whileTap={{ scale: 0.9 }}
                        >
                            <img 
                                src="/logo192.png" 
                                alt="My face" 
                                style={{ 
                                    width: '50px', 
                                    height: '50px', 
                                    borderRadius: '50%',
                                    marginRight: '1rem'
                                }}
                            />
                        </motion.div>
                        <Box>
                            <Typography variant="h5" component="h1" sx={{ fontWeight: 'bold' }}>
                                AVENA Clment
                            </Typography>
                            <Typography variant="subtitle1" sx={{ color: 'rgba(255,255,255,0.7)' }}>
                                TUDIANT 3E ANNE - BUT INFORMATIQUE - AIX EN PROVENCE
                            </Typography>
                        </Box>
                    </Box>

                    {!isMobile && (
                        <Box sx={{ display: 'flex', gap: 2 }}>
                            {menuItems.map((item) => (
                                <motion.div
                                    key={item.path}
                                    whileHover={{ y: -2 }}
                                    whileTap={{ y: 0 }}
                                >
                                    <Link
                                        to={item.path}
                                        style={{
                                            color: location.pathname === item.path ? '#3498db' : 'white',
                                            textDecoration: 'none',
                                            padding: '0.5rem 1rem',
                                            borderRadius: '4px',
                                            backgroundColor: location.pathname === item.path ? 'rgba(255,255,255,0.1)' : 'transparent'
                                        }}
                                    >
                                        {item.text}
                                    </Link>
                                </motion.div>
                            ))}
                        </Box>
                    )}
                </Toolbar>
            </AppBar>

            <Drawer
                variant="temporary"
                anchor="left"
                open={mobileOpen}
                onClose={handleDrawerToggle}
                ModalProps={{ keepMounted: true }}
                sx={{ display: { xs: 'block', md: 'none' } }}
            >
                <List sx={{ width: 250 }}>
                    {menuItems.map((item) => (
                        <ListItem 
                            key={item.path} 
                            component={Link} 
                            to={item.path}
                            onClick={handleDrawerToggle}
                            sx={{
                                backgroundColor: location.pathname === item.path ? 'rgba(52, 152, 219, 0.1)' : 'transparent',
                                '&:hover': {
                                    backgroundColor: 'rgba(52, 152, 219, 0.05)'
                                }
                            }}
                        >
                            <ListItemText primary={item.text} />
                        </ListItem>
                    ))}
                </List>
            </Drawer>

            <Container component="main" sx={{ flex: 1, py: 4 }}>
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -20 }}
                    transition={{ duration: 0.5 }}
                >
                    <Outlet />
                </motion.div>
            </Container>

            <Box
                component="footer"
                sx={{
                    py: 3,
                    px: 2,
                    mt: 'auto',
                    backgroundColor: '#2c3e50',
                    color: 'white',
                    textAlign: 'center'
                }}
            >
                <Typography variant="body2">
                    &copy; {new Date().getFullYear()} Mon CV
                </Typography>
            </Box>
        </Box>
    );
}

export default Layout;
