import React, {useEffect} from 'react';
import JobCard from "../components/JobCard";

function Experiences() {
    useEffect(() => {
        document.title = "Expriences";
    }, []);
    return (
        <div>
            <h3>Mes expriences professionelles</h3>
            <div className="cardHolder experiences">
                <JobCard company_image={"rda.png"} company_name={"Royaume des Arbres"} contract_type={"CDD"} duration={"Aot"} year={"2024"} role={"Oprateur"}/>
                <JobCard company_image={"sensia.png"} company_name={"Sensia"} contract_type={"Stage"} duration={"Avril - Juin"} year={"2024"} role={"Dveloppeur"}/>
                <JobCard company_image={"rda.png"} company_name={"Royaume des Arbres"} contract_type={"CDD"} duration={"Juillet - Aot"} year={"2023"} role={"Oprateur"}/>
                <JobCard company_image={"rda.png"} company_name={"Royaume des Arbres"} contract_type={"CDD"} duration={"Juillet - Aot"} year={"2022"} role={"Oprateur"}/>
            </div>
        </div>
    );
}

export default Experiences;